.recommended {
  display: grid;
  gap: 48px;

  @include on-tablet {
    gap: 56px;
  }

  &__products {
    @include page-grid;

    row-gap: 40px;

    @include on-tablet {
      gap: 56px;
    }

    @include on-desktop {
      column-gap: 24px;
    }
  }

  &__product {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 2 / -2;
    }

    @include on-desktop {
      grid-column: span 4;
    }
  }
}
