.section-title {
  @include bold-text;

  margin: 0;

  font-size: 32px;
  line-height: 32px;

  @include on-tablet {
    font-size: 40px;
    line-height: 40px;
    max-width: 496px;
  }

  @include on-desktop {
    font-size: 48px;
    line-height: 48px;
    max-width: 585px;
  }
}
