.categories {
  display: grid;
  row-gap: 48px;

  @include on-tablet {
    row-gap: 56px;
  }

  &__content {
    display: grid;
    row-gap: 40px;

    @include on-tablet {
      row-gap: 56px;
    }
  }

  &__view-all {
    @include on-desktop {
      max-width: 324px;
    }
  }
}
